<template>
  <div id="eduSchoolManagement">
    <div class="btns">
      <router-link :to="{ path: '/addschool' }" class="white-color el-button el-button--primary">
        <i class="el-icon-plus"></i>
        <span>新建学校</span>
      </router-link>
      <router-link :to="{ path: '/schoolbatch' }" class="white-color el-button el-button--primary">
        <i class="el-icon-document"></i>
        <span>批量导入</span>
      </router-link>
      <el-button @click="deleteAllDialog" type="primary" icon="delete2" :disabled="this.multipleSelection.length===0" style="float: right;">批量删除</el-button>
    </div>

    <div class="edu-box">
      <p class="edu-title">校归属机构：</p>
      <!--省教育局-->
      <div class="edu-city-con" v-if="userInfo.level == '1'">
        <span class="edu-city-list color333" :class="{'edu-active': eduCityActiveIndex===''}" @click="getListForAllEdu()">全部</span>
        <span class="edu-city-list" :class="{'edu-active': eduCityActiveIndex===index}" v-for="(item, index) in eduCityList" @click="getListAndEduList(item, index)">{{ item.name }}</span>
        <div class="edu-area-con" v-if="eduAreaList.length > 0">
          <span class="edu-area-list" :class="{'edu-active': eduAreaActiveIndex===index}" v-for="(item, index) in eduAreaList" @click="getListForOneEdu(item, index)">{{ item.name }}</span>
        </div>
      </div>
      <!--市教育局-->
      <div class="edu-city-con" v-if="userInfo.level == '2'">
        <span class="edu-city-list color333" :class="{'edu-active': eduAreaActiveIndex===''}" @click="getListForAllEdu2()">全部</span>
        <span class="edu-city-list" :class="{'edu-active': eduAreaActiveIndex===index}" v-for="(item, index) in eduAreaList" @click="getListForOneEdu2(item, index)">{{ item.name }}</span>
      </div>
      <!--县教育局-->
      <div class="edu-text" v-if="userInfo.level == '3'">{{ userInfo.orgName }}</div>
    </div>

    <div class="search">
      <el-form ref="searchData" :model="searchData" label-width="90px" :label-position="'right'">
        <el-row>
          <el-col :span="10">
            <el-form-item label="学校名称：">
              <el-input v-model="searchData.name" placeholder="请输入学校名称"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2" :offset="12">
            <el-button type="primary" icon="search" @click="search">查询</el-button>
          </el-col>
        </el-row>
        <template>
          <div class="more-con" v-show="searchMore">
            <el-form-item label="学制：">
              <el-checkbox-group v-model="searchData.codeTypes">
                <el-checkbox v-for="(item, index) in codeTypeList" :label="item.label" :class="{'ischecked': searchData.codeTypes.indexOf(item.label) >= 0}" :key="item.label">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label="学段：">
              <el-checkbox-group v-model="searchData.schoolTypes">
                <el-checkbox v-for="(item, index) in schoolTypeList" :label="item.label" :class="{'ischecked': searchData.schoolTypes.indexOf(item.label) >= 0}" :key="item.label">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label="地理位置：">
              <el-checkbox-group v-model="searchData.positions">
                <el-checkbox v-for="(item, index) in positionStrList" :label="item.label" :class="{'ischecked': searchData.positions.indexOf(item.label) >= 0}" :key="item.label">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </div>
        </template>
      </el-form>
      <div class="more-btn" @click="seeMore">
        <i class="el-icon-d-arrow-left" :class="{'arrow-down': arrowDown, 'arrow-up': !arrowDown}"></i>
        <span>更多</span>
      </div>
      <div class="search-bg"></div>
    </div>
    <div v-if="!tableDataIsNull" v-loading="listLoading">
      <div class="teacher-table tables mt40">
        <el-table
          :data="tableData"
          border
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55"
            align="center">
          </el-table-column>
          <el-table-column
            prop="name"
            label="学校名称"
            align="center"
            width="150"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="codeType"
            label="学制"
            align="center">
          </el-table-column>
          <el-table-column
            prop="schoolType"
            label="学段"
            align="center">
          </el-table-column>
          <el-table-column
            prop="positionStr"
            label="地理位置"
            align="center">
          </el-table-column>
          <el-table-column
            prop="fuserListStr"
            label="负责人"
            align="center"
            width="160"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="address"
            label="地址"
            align="center"
            width="130"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="状态"
            align="center">
            <template scope="props">
              <el-dropdown
                trigger="click"
                menu-align="start">
                <span class="el-dropdown-link" style="cursor: pointer;">
                  正常{{ props.row.state }}<i class="el-icon-caret-bottom el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>禁用</el-dropdown-item>
                  <el-dropdown-item>正常</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
              <!--<el-button type="text" size="small" @click="showstatePopup($event, props.row.id)">{{ props.row.state }}</el-button>-->
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            align="center"
            width="150">
            <template scope="props">
              <el-button type="text" size="small">
               <!-- <router-link :to="{ path: '/schoolinfo/detail/'+props.row.id }" class="link-text">查看</router-link>-->
                <router-link :to="{ path:  '/schoolrollfilerschoolmanagement/detail/'+props.row.id }" class="link-text">查看</router-link>
              </el-button>
              <el-button type="text" size="small">
                <router-link :to="{ path:  '/schoolrollfilerschoolmanagement/edit/'+props.row.id }" class="link-text">编辑</router-link>
              </el-button>
              <el-button @click="deleteOneDialog(props.row.id)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="teacher-pagination pagination-box mt40 mb40">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="searchData.start"
          :page-size="searchData.limit"
          layout="prev, pager, next, jumper"
          :total="tableDataTotal">
        </el-pagination>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="teacher-nodata" v-if="tableDataIsNull">
      没有学校信息
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'eduSchoolManagement',
    data () {
      return {
        userInfo: JSON.parse(localStorage.userInfo).education,  //用户信息
        //请求公共参数
        params: {
          educationId: JSON.parse(localStorage.userInfo).education.educationId, //机构id
          orgId: JSON.parse(localStorage.userInfo).education.orgId,  //当前区域id 210100 210000
          start: 1, //分页页码
          limit: 10,  //分页每页限制条数
        },
        //检索条件
        searchData: {
          name: '',
          codeTypes: [], //学制
          schoolTypes: [], //学段
          positions: []  //地理位置
        },
        codeTypeList: [{name:'六三制', label:'6-3'}, {name:'五四制', label:'5-4'}], //学制
        schoolTypeList: [{name:'小学', label:6}, {name:'完小', label:8}, {name:'初中', label:3}, {name:'九年一贯制', label:5}, {name:'高中', label:2}, {name:'完中', label:4}, {name:'职业高中', label:9}, {name:'普通高等学校', label:12}], //学段
        positionStrList: [{name:'中心城区', label:1}, {name:'城乡结合', label:2}, {name:'近郊', label:3}, {name:'远郊', label:4}, {name:'山区', label:5}],  //地理位置
        searchMore: false,  //true=>展示更多检索条件
        arrowDown: true, //向下箭头 false=>向上箭头
        eduCityList: [],  //市教育局列表
        eduCityActiveIndex: '',  //市教育局被选中教育局下标
        eduAreaList: [],  //区县教育局列表
        eduAreaActiveIndex: '',  //县教育局被选中教育局下标
        listLoading: false, //加载列表loading图
        tableDataIsNull: false, //数据库无学校
        tableData: [],  //列表集合
        tableDataTotal: 0,  //总数
        schoolId: 0, //当前学校id
        multipleSelection: [],  //多选集合
        state: '', //用户账号状态
        stateStyle: {  //状态设置框样式
          left: '-9999px',
          top: '-9999px',
          show: false,
          firstClick: true,  //第一次点击
          prevSchoolId: 0, //上一次被点击的学校id
          nowSchoolId: 0,  //当前被点击的学校id
        },
      }
    },
    methods:{
      //获取列表
      getList(params, isSearch){
        console.log(params);
        var _this = this;
        this.listLoading = true;
        this.$ajax('eduadmin/getManySchool', params).then(function(res) {
          _this.listLoading = false;
//          _this.$refs['searchData'].resetFields();
          _this.searchData = {
              name: '',
              codeTypes: [],
              schoolTypes: [],
              positions: []
          };
          if ( res.code === '200' ) {
            if ( res.data.total > 0 ) {
              _this.tableData = res.data.resultList;
              _this.tableDataTotal = res.data.total;
            } else {
              _this.tableDataIsNull = true;
            }
          } else {
//            if ( isSearch === 'search' ) {
//              _this.$message({
//                message: res.message,
//                type: 'warning'
//              });
//            } else {
              _this.$alert(res.message, '提示', {
                confirmButtonText: '确定',
                callback: () => {
                  if ( _this.userInfo.level == '1' ) {
                    _this.getListForAllEdu();
                  } else if ( _this.userInfo.level == '2' ) {
                    _this.getListForAllEdu2();
                  }
                }
              });
//            }
          }
        }, function(err) {})
      },
      //获取教育局列表
      getOrgList(type) {
        var _this = this;
        var type = type || this.userInfo.level;
        var params = {parentOrgId: this.params.orgId};
        if ( type == '3' ) return;
        this.$ajax('eduadmin/orgChildrenInfo', params).then(function(res) {
          if ( res.code === '200' ) {
            if ( type == '1' ) {
              _this.eduCityList = res.data;
            } else if ( type == '2' ) {
              _this.eduAreaList = res.data;
            }
          }
        }, function(err) {})
      },
      //省教育局登陆——获取某个市教育局下的县教育局列表及所有学校列表
      getListAndEduList(item, index) {
        this.params.orgId = item.orgId;
        this.eduCityActiveIndex = index;
        this.eduAreaActiveIndex = '';
        this.getOrgList('2');
        this.getList(this.params);
      },
      //省教育局登陆——获取某县教育局下所有学校列表
      getListForOneEdu(item, index) {
        this.params.orgId = item.orgId;
        this.eduAreaActiveIndex = index;
        this.getList(this.params);
      },
      //省教育局登陆——全部
      getListForAllEdu() {
        this.params.orgId = this.userInfo.orgId;
        this.eduCityActiveIndex = '';
        this.eduAreaActiveIndex = '';
        this.eduAreaList = [];
        this.getList(this.params);
      },
      //市教育局登陆——获取某县教育局下所有学校列表
      getListForOneEdu2(item, index) {
        this.params.orgId = item.orgId;
        this.eduAreaActiveIndex = index;
        this.getList(this.params);
      },
      //市教育局登陆——全部
      getListForAllEdu2() {
        this.params.orgId = this.userInfo.orgId;
        this.eduAreaActiveIndex = '';
        this.getList(this.params);
      },
      //显示更多筛选条件
      seeMore() {
        this.arrowDown = !this.arrowDown;
        this.searchMore = !this.searchMore;
      },
      //搜索
      search() {
        var searchParams = {};
        for ( let i in this.searchData ) {
          searchParams[i] = this.searchData[i].toString();
        }
        var params = Object.assign({}, this.params, searchParams);
        this.getList(params, 'search');
      },
      //多选
      handleSelectionChange(val) {
        this.multipleSelection = '';
        if ( val.length > 0 ) {
          for ( var i=0; i<val.length; i++ ) {
            this.multipleSelection += val[i].id + ',';
          }
          this.multipleSelection = this.multipleSelection.substring(0, this.multipleSelection.length-1);
        }
      },
      //删除单个学校确认框
      deleteOneDialog(id) {
        var _this = this;
        this.$confirm('确定要删除选中学校吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function() {
          _this.schoolId = id;
          _this.deleteOne();
        }).catch(function() {
          console.log('取消')
        })
      },
      //批量删除学校确认框
      deleteAllDialog() {
        var _this = this;
        this.$confirm('确定要删除选中学校吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function() {
          _this.deleteAll();
        }).catch(function() {
          console.log('取消')
        })
      },
      //删除
      deleteOne() {
        var _this = this;
        var params = Object.assign({}, {schoolId: this.schoolId});
        this.$ajax('eduadmin/deleteOneSchool', params).then(function(res) {
          if ( res.code === '200' ) {
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.getList(_this.params);
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.params);
              }
            });
          }
        }, function(err) {})
      },
      //批量删除
      deleteAll() {
        var _this = this;
        var params = {schoolIds: this.multipleSelection.toString()};
        this.$ajax('eduadmin/deleteManySchool', params).then(function(res) {
          if ( res.code === '200' ) {
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.multipleSelection = [];
            _this.getList(_this.params);
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.params);
              }
            });
          }
        }, function(err) {})
      },
      //设置状态
      setState(state) {
        var _this = this;
        var params = Object.assign({}, {SchoolId: this.SchoolId, state: state});
        this.$ajax('schadmin/deleteOneTeacher', params).then(function(res) {
          if ( res.code === '200' ) {
            console.log('状态更改成功');
            this.stateStyle.show = false;
            _this.getList(_this.searchData);
          }
        }, function(err) {})
      },
      //跳页
      handleCurrentChange(val) {
        console.log('第'+val+'页');
        this.params.start = val;
        this.getList(this.params);
        this.getOrgList();
      }
    },
    beforeMount(){
      this.getList(this.params);
      this.getOrgList();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #eduSchoolManagement { padding: 0 20px 20px; min-height: 400px; color: #666;}
  #eduSchoolManagement .white-color { color: #fff;}
  #eduSchoolManagement .link-text { color: #20a0ff;}
  #eduSchoolManagement .btns { border-bottom: 1px solid #ECEDEF; padding-bottom: 15px; padding-top: 15px;}
  #eduSchoolManagement .mt20 { margin-top: 20px;}
  #eduSchoolManagement .mt40 { margin-top: 40px;}
  #eduSchoolManagement .mb40 { margin-bottom: 40px;}
  #eduSchoolManagement .pagination-box { float: right;}
  #eduSchoolManagement .teacher-nodata { background: url("../../../resources/front/imgs/nodata.png") no-repeat center; text-align: center; padding-top: 100px; min-height: 300px;}
  #eduSchoolManagement .edu-box { position: relative;}
  #eduSchoolManagement .edu-text { position: absolute; left: 100px; top: 10px; color: #20a0ff;}
  #eduSchoolManagement .edu-title { padding: 10px 0; color: #333;}
  #eduSchoolManagement .edu-city-con { background: #F9F9F9; padding: 10px;}
  #eduSchoolManagement .edu-city-list { display: inline-block; margin-right: 20px; line-height: 1.6; font-size: 14px; cursor: pointer;}
  #eduSchoolManagement .color333 { color: #333;}
  #eduSchoolManagement .edu-active { color: #20a0ff!important;}
  #eduSchoolManagement .edu-area-con { padding: 10px;}
  #eduSchoolManagement .edu-area-list { display: inline-block; margin-right: 16px; line-height: 1.6; font-size: 12px; color: #858585; cursor: pointer;}
  #eduSchoolManagement .search { padding: 20px 20px 20px 80px; border: 1px solid #cedaea; border-top:3px solid #0d9ee9; margin-top: 18px; position: relative;}
  #eduSchoolManagement .search-bg { position: absolute; left: 12px; top: -11px; z-index: 10; width: 56px; height: 61px; background: url("../../../resources/front/imgs/search-bg.png") no-repeat;}
  #eduSchoolManagement .more-btn { position: absolute; right: 118px; top: 30px; color: #20A0FF; font-size: 12px; cursor: pointer;}
  #eduSchoolManagement .more-btn:hover { color: #4db3ff}
  #eduSchoolManagement .more-btn span, #eduSchoolManagement .more-btn i { vertical-align: middle;}
  #eduSchoolManagement .arrow-down { transform: rotate(270deg);}
  #eduSchoolManagement .arrow-up { transform: rotate(90deg);}
  #eduSchoolManagement .more-con .el-checkbox__inner{ display: none;}
  #eduSchoolManagement .more-con .el-checkbox__label { padding-left: 0;}
  #eduSchoolManagement .more-con .el-checkbox { border: 1px solid #e5e5e5; padding: 0 10px; line-height: 30px; transition: 0.2s;}
  #eduSchoolManagement .more-con .el-checkbox:hover { border-color: #ccc;}
  #eduSchoolManagement .more-con .ischecked { border-color: #20A0FF!important; color: #20A0FF!important;}
  #eduSchoolManagement .more-con .el-checkbox+.el-checkbox { margin-left: 5px;}
  #eduSchoolManagement .more-con .el-form-item { margin-bottom: 10px;}
</style>



